/* SlotMachine.css */

/* Stili di base per il contenitore principale, adattati per essere più flessibili */
.slot-page-content {
  max-width: 90%; /* Limita la larghezza al 90% dello schermo per schermi più grandi */
  width: auto; /* Lascia che la larghezza si adatti automaticamente */
  min-width: 300px; /* Larghezza minima per evitare che diventi troppo piccolo su mobile */
  min-height: 450px; /* L'altezza minima può rimanere, ma sii consapevole che su schermi molto piccoli potrebbe causare scroll */
  margin: 40px auto;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9f9;
  color: #333;
  padding: 30px; /* Riduci il padding di default per schermi più piccoli */
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative; /* Importante per lo z-index del popup, se ci sono altri elementi fixed/absolute */
  z-index: 1; /* Assicurati che il contenuto della slot sia sotto il popup dell'immagine */
}

.slot-message {
  height: 80px; /* Altezza fissa per il messaggio */
  width: 100%;
  font-size: 18px;
  color: #000;
  margin-top: 20px;
  margin-bottom: 0;
  padding: 0 10px; /* Riduci il padding orizzontale */
  overflow-wrap: break-word;
  overflow: hidden;
  white-space: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

h2 {
  margin-bottom: 10px;
  color: #4a90e2;
}

/* Stili base per l'immagine sara.png */
.slot-header-image {
  height: 250px; /* Riduci l'altezza per schermi più piccoli */
  max-width: 100%; /* Assicura che l'immagine non superi la larghezza del contenitore */
  object-fit: contain; /* Adatta l'immagine mantenendo le proporzioni */
  margin: 0 auto 30px; /* Riduci il margine inferiore */
  cursor: pointer; /* Cambia il cursore in una mano per indicare che è cliccabile */
  transition: transform 0.3s ease; /* Aggiunge una transizione fluida per l'ingrandimento */
  display: block; /* Importante per il margin auto */
}

/* Stili per l'immagine di Sara ingrandita - Questi devono sovrascrivere tutto */
.slot-header-image.zoomed {
  position: fixed; /* L'immagine sovrappone tutto il resto */
  top: 0;
  left: 0;
  width: 100vw; /* Occupa tutta la larghezza della viewport */
  height: 100vh; /* Occupa tutta l'altezza della viewport */
  object-fit: contain; /* Mostra l'intera immagine senza tagli, mantenendo le proporzioni */
  background-color: rgba(0, 0, 0, 0.8); /* Sfondo scuro per far risaltare l'immagine */
  z-index: 10000; /* Assicura che sia sopra a tutti gli altri elementi (inclusi popup) */
  cursor: zoom-out; /* Indica che è possibile cliccare per tornare indietro */
  padding: 20px; /* Aggiunge un po' di padding per evitare che l'immagine si attacchi ai bordi */
  box-sizing: border-box; /* Include il padding nella dimensione totale */
  display: flex; /* Utilizza flexbox per centrare l'immagine all'interno dell'area zoomata */
  justify-content: center;
  align-items: center;

  /* Resetting styles that might interfere with fixed positioning/full size */
  margin: 0 !important; /* Rimuovi tutti i margini esterni per la versione ingrandita */
  border-radius: 0 !important; /* Rimuovi bordi arrotondati se applicati altrove */
  box-shadow: none !important; /* Rimuovi ombre per un look più pulito */
  transform: none !important; /* Annulla qualsiasi trasformazione di base o hover */
  height: 100vh !important; /* Forza l'altezza al 100% della viewport */
  width: 100vw !important; /* Forza la larghezza al 100% della viewport */
  max-width: unset !important; /* Rimuovi qualsiasi limite di larghezza massima */
  pointer-events: all; /* Assicura che sia cliccabile per tornare indietro */
}


.slot-reels {
  display: flex;
  justify-content: center;
  gap: 15px; /* Riduci lo spazio tra i rulli */
  margin-bottom: 20px;
}

.reel-symbol {
  font-size: 3.5rem; /* Riduci la dimensione del font per i simboli */
  background: #e0f0ff;
  border-radius: 12px;
  width: 70px; /* Riduci la larghezza dei rulli */
  height: 70px; /* Riduci l'altezza dei rulli */
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px rgba(74, 144, 226, 0.3);
  user-select: none;
  cursor: default;
  transition: transform 0.2s ease;
}

.reel-symbol:hover {
  transform: scale(1.1);
}

.winner-message {
  color: #28a745; /* verde brillante */
  text-shadow: 0 0 5px #28a74588;
  font-size: 1.1em; /* Assicurati che sia leggibile */
}

.spin-button {
  background-color: #4a90e2;
  border: none;
  margin-top: 10px;
  padding: 15px 80px; /* Riduci il padding per il bottone */
  border-radius: 30px;
  color: white;
  font-weight: 600;
  font-size: 18px; /* Riduci la dimensione del font */
  cursor: pointer;
  box-shadow: 0 6px 12px rgba(74, 144, 226, 0.5);
  user-select: none;
  outline: none;
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
  width: 90%; /* Il bottone occupa quasi tutta la larghezza su mobile */
  max-width: 350px; /* Limita la larghezza massima del bottone */
}

.spin-button:disabled {
  background-color: #a3c4f3;
  color: #eee;
  cursor: default;
  box-shadow: none;
}

.spin-button:not(:disabled):hover {
  background-color: #357abd;
}

.spin-button:not(:disabled):active {
  transform: scale(0.95);
}

/* --- Media Queries --- */

/* Per schermi molto piccoli (ad esempio, larghezza inferiore a 400px) */
@media (max-width: 400px) {
  .slot-page-content {
    min-width:300px; /* Larghezza minima inferiore per schermi molto stretti */
    padding: 10px; /* Padding ulteriormente ridotto */
    margin: 20px auto; /* Riduci il margine verticale */
  }

  .slot-header-image {
    height: 200px; /* Riduci ulteriormente l'altezza dell'immagine */
    margin-bottom: 20px;
  }
  /* L'immagine ingrandita non è influenzata dalle media query più piccole */
  .slot-header-image.zoomed {
    /* Le proprietà 'fixed' e '100vw/vh' garantiscono che superi sempre i limiti */
    /* Nessuna modifica necessaria qui per ingrandimento su schermi piccoli */
  }


  .slot-reels {
    gap: 10px; /* Riduci ulteriormente lo spazio tra i rulli */
  }

  .reel-symbol {
    font-size: 3rem; /* Riduci ancora la dimensione del font */
    width: 60px; /* Riduci la larghezza dei rulli */
    height: 60px; /* Riduci l'altezza dei rulli */
  }

  .slot-message {
    font-size: 16px; /* Riduci la dimensione del font per il messaggio */
    height: 70px;
  }

  .spin-button {
    font-size: 16px; /* Riduci la dimensione del font del bottone */
    padding: 12px 60px;
  }
}

/* Per schermi medi (tablet in verticale, ad esempio, tra 600px e 900px) */
@media (min-width: 600px) and (max-width: 900px) {
  .slot-page-content {
    max-width: 70%;
    padding: 40px;
  }

  .slot-header-image {
    height: 280px;
  }
  /* L'immagine ingrandita non è influenzata dalle media query più grandi */
  .slot-header-image.zoomed {
    /* Nessuna modifica necessaria qui per ingrandimento su schermi medi */
  }

  .slot-reels {
    gap: 25px;
  }

  .reel-symbol {
    font-size: 4.5rem;
    width: 90px;
    height: 90px;
  }

  .spin-button {
    padding: 20px 100px;
    font-size: 22px;
  }
}

/* Per schermi grandi (desktop, ad esempio, oltre 900px) */
@media (min-width: 901px) {
  .slot-page-content {
    max-width: 500px; /* Riporta alla larghezza fissa originale per desktop */
    width: 500px;
    padding: 50px;
  }

  .slot-header-image {
    height: 300px;
    margin: 0 auto 50px;
  }
  /* L'immagine ingrandita non è influenzata dalle media query più grandi */
  .slot-header-image.zoomed {
    /* Nessuna modifica necessaria qui per ingrandimento su desktop */
  }

  .slot-reels {
    gap: 20px;
  }

  .reel-symbol {
    font-size: 4rem;
    width: 80px;
    height: 80px;
  }

  .spin-button {
    padding: 20px 150px;
    font-size: 20px;
    width: auto; /* Lascia che la larghezza si adatti al padding */
  }
}